.customer__slide a {
  background: none;
  border: none;
}

a.customer__slide-prev, .customer__slider:before {
  left: 15%;
  transform: rotate(135deg);
}

a.customer__slide-next, .customer__slider:after {
  right: 15%;
  transform: rotate(-45deg);
}

.customer__slider:before, .customer__slider:after, .customer__slide-prev, .customer__slide-next {
  width: 35px;
  height: 35px;
  box-sizing: border-box;
  border: 4px solid rgba(0, 0, 0, 0);
  border-width: 0 4px 4px 0;
  padding: 40px;
  position: absolute;
  top: 48%;
}

.customer__slider:before, .customer__slider:after {
  content: "";
  z-index: 1;
  pointer-events: none;
  background: none;
}

